@import url(https://fonts.googleapis.com/css2?family=DM+Sans:wght@400;500;700&display=swap);
*{
  margin: 0%;
  padding: 0%;
  outline: none;
  border: none;
}
body {
  margin: 0;
  font-family: 'DM Sans';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.head {
    display: flex;
    height: 100vh;
}

.form {
    flex: 40% 1;
    /* margin: 30vh 20vh 50vh; */
    margin: 20vh 10vw 10vh 15vw;
    padding: 30px;

    background: #FFFFFF;
    box-shadow: 0px 4px 84px rgba(0, 0, 0, 0.14);
    border-radius: 9px;
    /* font-size: 1.3em; */
}

.form h2 {
    margin: 10px 0px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content; 
    font-weight: 500;
    margin-left: 0px;
    color: #313C61;
}



.form input {
    padding: 8px;
    margin: 20px 0px;
    width: 95.5%;
    height: 40px;
    background: #F4F4F4;
    border-radius: 8px;
    font-size: 1em;
}

.form #submit {
    width: 100%;
    margin: auto;
    color: white;
    font-weight: 700;
    font-size: medium;
    transition: ease-in .2s;
    background: #575CE5;
    border-radius: 8px;
}

.form #submit:hover {
    background: #1419a1;
    ;
}

.flex-hr {
    display: flex;
    margin-top: 10px;
    align-items: center;
}

.flex-hr p {
    width: 10%;
    text-align: center;
    color: rgba(0, 0, 0, 0.5);
}

.g-btn-div {
    margin-top: 10px;
}

.g-logo {
    background: url(data:image/png;base64,);
    width: 30px;
    margin: 0px 5px;
    height: 27px;
}

.g-btn {
    width: 100%;
    display: flex;
    margin: auto;
    padding: 10px 0px;
    transition: ease-in .2s;
    border: 1px solid #575CE5;
    border-radius: 8px;
    color: #575CE5;
    align-items: center;
    justify-content: center;
}

.form .g-btn:hover {
    color: #1419a1;
    ;
}

.signup-p {
    font-weight: 500;
    font-size: 18px;
    text-align: center;
    color: #313C61;
    margin-top: 15px;
}

.signup-link {
    color: #575CE5;
}

.pic-div {
    flex: 90% 1;
    background-color: #c4d4d3;
}

.pic {
    background: url(demo/static/media/loginpic.0d90c2a0.png);
    background-size: contain;
    background-repeat: no-repeat;
    height: 100vh;
    background-position: right;
}
.page {
    padding: 30px;
    margin-top: 150px;
    margin-left: 50px;
}

h1 {
    font-family: 'DM Sans';
    font-style: normal;
    font-weight: 700;
    font-size: 56px;
    line-height: 73px;

    color: #313C61;
}

.p p {
    font-family: 'DM Sans';
    font-style: normal;
    font-weight: 400;
    font-size: 35px;
    line-height: 46px;

    color: #313C61;
}

.search {
    width: 860px;
    height: 93px;
    margin-top: 50px;
    margin-left: 30px;
    background: rgba(255, 255, 255, 0.74);
    box-shadow: 0px 4px 32px rgba(0, 0, 0, 0.08);
    border-radius: 59px;
    display: flex;
    justify-content: space-evenly;
}

::-webkit-input-placeholder {
    font-family: 'DM Sans';
    font-style: normal;
    font-weight: 500;
    font-size: 26px;
    line-height: 34px;
    color: rgba(131, 144, 187, 0.46);
    /* padding: 10px; */
}

.search input {
    font-family: 'DM Sans';
    font-style: normal;
    font-weight: 500;
    font-size: 26px;
}

.search select {
    font-family: 'DM Sans';
    font-style: normal;
    font-weight: 500;
    font-size: 26px;
    line-height: 34px;
    /* identical to box height */

    text-align: right;

    color: #8390BB;
}

.search button {
    background-color: white;
}


